    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                typeList: [[]],   // 请假类型列表
                value: ['请选择'],
                prototype: [],   //  后端原型请假类型
                valueId: null,  // 请假类型ID
                startTime: "",  //  开始时间
                entTime: "",  //  结束时间
                text: "",   //   问题描述

                show: false,   // 审批人弹窗
                getUser: '',   //  审批人头像
                examineName: null,   //  审批人姓名
                examineNameID: null,   //  审批人姓名ID
                examineSection: '', //  部门ID值
                nameID: '',  // 审批人ID值
                examineNum: 0, // 控制审批人模块显示隐藏  0为隐藏1为显示
                dutylist: [],  // 审批部门列表
                namelist: [],  // 审批人员列表

                showho: false,   // 抄送人弹窗
                copyName: '',    //  抄送个人头像
                receptionName: null,   //  抄送人姓名
                receptionNameId: null,   //  抄送人ID
                receptionSection: '',  // 抄送人部门
                copyToList: [],  //  抄送部门列表
                copyNameList: [],   //  抄送人员列表
                copyImg: [],  //  抄送人信息数组
                copyId: [],  //  抄送人ID数组

                imgList: [],     //  图面预览列表
                imgcode: [],    //  通过请求后天之后返回的头像路径
                limit:6, //限制图片上传的数量
                options: {
                    getThumbBoundsFn (index) {
                        let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]
                        let pageYScroll = window.pageYOffset || document.documentElement.scrollTop
                        let rect = thumbnail.getBoundingClientRect()
                        return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}
                    }
                },
            }
        },
        created(){
            // 获取请假类型
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.index,
                type: "post",
                data: {

                },
                success: (response, allResponse) => {
                    // console.log(response)
                    this.prototype = response.leave_type;
                    this.prototype.forEach((item,index)=>{
                        this.typeList[0].push(item.type_name)
                    })
                }
            });
        },
        methods:{
            baocun(){
                this.prototype.forEach((item,index)=>{
                    if(this.value == item.type_name){
                        this.valueId = item.id
                    }
                });
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.apply,
                    type: "post",
                    data: {
                        type: 1,
                        start_time: this.startTime,
                        end_time: this.entTime,
                        leave_type: this.valueId,
                        remark: this.text,
                        img: this.imgcode,
                        approval: this.nameID,
                        send_people: this.copyId
                    },
                    success: (response, allResponse) => {
                        this.$vux.toast.text(allResponse.msg, 'middle');
                        setTimeout(()=>{
                            this.$router.push('/manage')
                        },900)
                    }
                });
            },
//            点击添加审批人
            addDuty(){
                this.show = true;
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getApprovalDepartment,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        // console.log(response)
                        this.dutylist = response
                    }
                });
            },
            // 根据ID获取审批人员列表
            gainbranch(){
                // console.log(this.examineSection)
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getApprovalDepartmentUser,
                    type: "post",
                    data: {
                        department_id: this.examineSection
                    },
                    success: (response, allResponse) => {
                        this.namelist = response
                        console.log(response)
                    }
                });
            },
//            点击添加审批人弹窗确定按钮
            onConfirm(){
                this.dutylist.forEach((item,index) => {
                    if( item.key == this.examineSection ){
                        this.examineSection = item.value
                    }
                });
                this.namelist.forEach((item,index) => {
                    if( item.key == this.examineName ){
                        this.examineName = item.value;
                        this.nameID = item.key
                    }
                });
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getUserInfo,
                    type: "post",
                    data: {
                        user_id: this.nameID
                    },
                    success: (response, allResponse) => {
                        this.getUser = response
                    }
                });
                this.examineNum = 1;
                if(this.examineSection == null && this.examineName == null){
                    this.examineNum = 0
                }
            },
//            点击添加抄送人
            addreception(){
                this.showho = true
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getHumanUserDepartment,
                    type: "post",
                    data: {

                    },
                    success: (response, allResponse) => {
                        console.log(response)
                        this.copyToList = response
                    }
                });
            },
            // 根据ID获取抄送人员列表
            copy(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getApprovalDepartmentUser,
                    type: "post",
                    data: {
                        department_id: this.receptionSection
                    },
                    success: (response, allResponse) => {
                        // console.log(response)
                        this.copyNameList = response
                    }
                });
            },
//            点击添加抄送人弹窗确定按钮
            onreception(){
                // console.log('我点击了确定',this.receptionSection,this.receptionName)
                this.copyToList.forEach((item,index) => {
                    if( item.key == this.receptionSection ){
                        this.receptionSection = item.value
                    }
                });
                this.copyNameList.forEach((item,index) => {
                    if( item.key == this.receptionName ){
                        this.receptionName = item.value;
                        this.receptionNameId = item.key
                    }
                });
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getUserInfo,
                    type: "post",
                    data: {
                        user_id: this.receptionNameId
                    },
                    success: (response, allResponse) => {
                        this.copyName = response;
                    }
                });
                if(this.copyImg.length > 0){
                    if(this.copyId.indexOf(this.receptionNameId) == -1){
                        this.copyImg.push({
                            url: this.copyName.face,
                            name: this.receptionName,
                        });
                        this.copyId.push(this.receptionNameId);
                    }else{
                        this.$vux.toast.text('该抄送人已存在', 'middle')
                    }
                }else{
                    this.copyImg.push({
                        url: this.copyName.face,
                        name: this.receptionName,
                    });
                    this.copyId.push(this.receptionNameId);
                }
            },
            // 点击删除抄送人
            dele(index){
                this.copyImg.splice(index, 1);
                this.copyId.splice(index, 1);
            },
            // 点击图片进行放大观看
            magnfiy (index) {
                console.log(this.imgList)
                this.$refs.previewer.show(index)
            },
            // 点击上传图片
            fileChange(e){
                let file = e.target.files[0];
                var reader = new FileReader();
                reader.onload = (data) => {
                    let res = data.target || data.srcElement;
                };
                reader.readAsDataURL(file);
                let param = new FormData(); //创建form对象
                param.append('file',file,file.name);//通过append向form对象添加数据
                let config = {
                    headers:{'Content-Type':'multipart/form-data'}
                };  //添加请求头
                this.$ajax.post(this.common.apiUrl.feedback,param,config)
                    .then(response=>{
                        // console.log(response.data.data);
                        this.imgList.push({
                            msrc: response.data.data.src,
                            src: response.data.data.src
                        });
                        this.imgcode.push(response.data.data.src);
                        console.log(this.imgcode);
                    })
            },
            // 删除上传的图片
            delImg(index) {
                this.imgList.splice(index, 1);
                this.imgcode.splice(index, 1);
                if (this.limit !== undefined) this.limit = 6-this.imgList.length;
            },
        }
    }



